export default {"name":"老帕卡茶","img":"\\heicha\\laopakacha\\img_20191122132713lqtuyw.jpg","desc":["“老帕卡”，是哈尼族话“茶树的老叶子”的意思，是云南产茶区茶农当作日常不可缺少的饮品，原来并不外卖。此茶品从采摘到做成成品均为哈尼山民纯手工用传统原始的加工工艺，不含任何添加及化学原料，实属纯天然纯绿色的独特茶品。云南哈尼族纯手工老帕卡茶，采用古茶树老黄片手工捆制而成，因手工制作，每条重量不等约为100克至200克之间，一般不以重量为准，当地老百姓按条来卖。"],"menus":[{"name":"老帕卡茶茶叶特点","content":[{"text":"老帕卡，并不是我们所认为的地名。云南是少数民族聚居地，很多少数民族的寨子中均有茶树，茶叶便成为他们日常生活中必需品。但即使是茶叶也会产生很多种不同的吃法、喝法。如：拉祜族的烤茶、竹筒茶;白族的三道茶;布朗族酸腌茶等。"},{"src":"\\heicha\\laopakacha\\img_20191122132713lljzgo.jpg"},{"text":"老帕卡在哈尼话中意思是“茶树的老叶子”，在云南思茅地区的一些哈尼族中，他们会在冬季，将茶树上新鲜的老叶子(茶叶嫩度较成熟、对夹叶)采摘下来，带回家中，以特殊的制茶工艺制成。"}]},{"name":"老帕卡茶加工工艺","content":[{"text":"“老帕卡”是采摘茶树上的新鲜的老叶子，经放在沸水锅里煮(或放木甑中蒸)让它均匀受热，等茶叶变成黄色后，用小火把水焙干，这个过程也要不停的翻动茶叶避免水干糊茶。水干了把它拿出来放在簸箕上晾凉，然后揉茶。“老帕卡”的这种杀青方式又称“捞青”(或蒸青)。揉过的茶抖散开来分装在簸箕待到叶子除去部份水份，放入竹筒制作成圆条型，用藤条捆绑，置于阳光下晾晒，晒干后即成“老帕卡”筒茶。"}]},{"name":"老帕卡茶储藏方法","content":[{"text":"一、省钱省力的纸箱存放"},{"text":"如果你不是茶商或者没有收藏茶叶的习惯，只是喜欢喝茶。那么这个方法是最省钱省力，并且非常实用。先用塑料袋将茶叶装好，然后将茶叶放置在纸箱当中。切记不能将纸箱放在靠墙或者地上，要尽量避免纸箱直接接触到墙壁、地板。"},{"text":"因为纸箱会吸收墙壁或者地面的水汽，导致茶叶潮湿、发霉，特别是广东、福建、浙江、上海等地，这些地方的空气湿度较大，不注意容易导致茶叶受潮。最好将装好茶叶的纸箱放在阴凉、干燥、通风地方，比如书柜、木制架子之类的。"},{"text":"存储茶叶需要特别注意的时期：“梅雨季节”，每年到这个时候墙上都会有水珠。如果放置茶叶的纸箱挨墙壁或者地面太近很容易受潮，建议在梅雨季节，茶友最好定期查看，检查一下茶叶是否受潮。"},{"text":"除此之外，存储老帕卡茶还要避免被阳光照射，长期被阳光照射会导致茶叶变质。"},{"src":"\\heicha\\laopakacha\\img_20191122132713r9reh1.jpg"},{"text":"二、强烈推荐的罐子存法"},{"text":"罐子存储茶叶的方法比较科学，因为罐子存储茶叶能有效的防止茶叶受潮，还能避免与空气接触，产生氧化反应，另外，罐子存茶还有醒茶的效果，可以说是一举两得。推荐罐子：陶罐或者紫砂罐。"},{"text":"存储老帕卡茶对光线、温度、空气湿度都有很高的要求，如果存储不当，很容易造成茶叶变味。茶容易串味，所以放置在纸箱之前需要用塑料袋密封好，目的除了防止受潮之外，就是防止串味。"},{"text":"罐子存储茶叶有两个选择："},{"text":"1、用小罐子存储，每一个罐子放置一种茶叶。"},{"text":"2、用大罐子存储，不能将所有茶叶混合放在大罐子之中，每一种茶叶需要用塑料袋封起来，然后再放置在大罐子当中。"},{"text":"三、散茶的存放"},{"text":"散茶最好不要存储，因为散茶没有被压制过，极易散味。存储一至两年的散茶香味、口感都已经大不如从前。短期存储散茶可以用塑料袋封好或者存放在紫砂罐当中(注：一定要密封存储)。"},{"text":"很多茶友会用冰箱来存储茶，这个存储方法是错误的。老帕卡茶在存储过程中会不断变化，如果放置在冰箱中，茶叶中的内含物质会被遏制，自然不可能“越陈越香”。除此之外，冰箱长期放置各种食物，老帕卡茶放置在冰箱中容易吸收其它异味。"}]},{"name":"老帕卡茶冲泡方法","content":[{"text":"老帕卡冲泡饮用"},{"text":"饮用方法有：烤茶(火烤后冲入沸水)、泡着喝(普洱茶的泡法)、煮着喝，烤和煮，茶的滋味不一样，煮茶出糖香，烤茶出炒香。"},{"src":"\\heicha\\laopakacha\\img_2019112213271321dw1c.jpg"},{"text":"老帕卡冲泡要点"},{"text":"1.老帕卡是高温茶，不能用开水冲泡，只能用茶壶煮着饮用，小火加温后慢慢煮泡，等着茶汤的香味释放出来，茶汤能够品出甜味后才能饮用。煮茶的时间按照茶客的喜好来定。"},{"text":"2.老帕卡在冲泡之前需要在炭火上稍微烘烤，等着茶叶的香气释放出来再放入茶壶中慢慢熬制。"},{"text":"喝“老帕卡”下去时稠稠的、滑滑的、细细的、甜甜的，汤色金黄透彻，没有一丝一毫的杂质，回甘迅捷明亮，香味纯正清冽，但这香却是闻不到的，只可以用口腔慢慢去体会。老帕卡的香有别于熟普洱的陈香或是青饼的清香、蜜香、花果香、草木香，那仿佛是一种记忆中的味道，是儿时家中散发出的那种清贫但真实而温暖的味道。"}]},{"name":"老帕卡茶功效作用","content":[{"text":"1、夏季蚊虫最多，热浪袭人，人的身上很容易生各种皮炎疹子，遇到这种情况别急，不用去买西药软膏什么的!您用几片老帕卡和一勺盐煮成浓茶汤，趁热用汤洗患处。"},{"text":"2、老帕卡我们建议您煮饮，因为煮饮，老帕卡的特有味道才能全部释出在茶汤中。"},{"text":"3、因老帕卡只是经过“捞青”杀青，故冲泡过后叶底完整，晾干即是一张野生古乔木茶树树叶的标本，有纪念意义，也可以当书签用。"}]}]}